@import url('https://fonts.googleapis.com/css2?family=Lora:ital,wght@0,400;0,700;1,400&family=Open+Sans:wght@400;700&display=swap');

$font-family-serif: 'Lora', serif;
$font-family-sans: 'Open Sans', sans-serif;

// old font faces can be removed or kept as fallback
@font-face {
  font-family: 'PT Sans';
  src: local('PT Sans'), local('PTSans-Regular'), url('../fonts/PTSans-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'PT Sans';
  src: local('PT Sans Bold'), local('PTSans-Bold'), url('../fonts/PTSans-Bold.woff') format('woff');
  font-weight: bold;
  font-style: normal; }
