@import 'index';

body {
  display: flex;
  flex-direction: column; }

article {
  flex-grow: 1; }

body > header, body > footer {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between; }

body > header, body > article, body > footer {
  padding: 2em 1em; // More forgiving padding for mobile
  max-width: 800px;
  margin: 0 auto;
  width: 100%;
  box-sizing: border-box; } // Ensures padding is included in the width

@media (min-width: 840px) {
  body > header, body > article, body > footer {
    padding: 2em; } } // Restore larger padding on wider screens

article header {
  margin-bottom: 1.5em; }

article header h1 {
  font-size: 2.2em;
  margin: 0 0 .2em; }

nav {
  margin: .5em -1.5em; }

nav a {
  margin: .5em 1.5em;
  font-family: $font-family-sans;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 0.9em; }
